<template>
  <div>
    <Add v-loading="pageLoading" :typeList="typeList" :tagList="tagList" @saveSuccess="getApplicationList" />
    <el-table :data="pageInfo" v-loading="pageLoading">
      <el-table-column prop="application_name" label="软件名（英文）"></el-table-column>
      <el-table-column prop="application_name_zh" label="软件名（中文）"></el-table-column>
      <el-table-column prop="file_name" label="文件名"></el-table-column>
      <el-table-column prop="add_time" label="上传日期" :formatter="formatterTime"></el-table-column>
      <el-table-column prop="contributor" label="维护者"></el-table-column>
      <el-table-column prop="status" :formatter="formatterStatus" label="审核状态" />
    </el-table>
  </div>
</template>
<script>
import axios from "axios";
import dayjs from 'dayjs';
import Add from "../components/upload_application/add";

export default {
  components: { Add },
  data() {
    return {
      pageInfo: [],
      typeList: [],
      pageLoading:false,
      tagList:[]
    };
  },

  methods: {
    formatterTime (row, column, cellValue, index) {
        return cellValue ? dayjs(cellValue).format('YYYY-MM-DD HH:mm:ss') : '--'
    },
    formatterStatus(row, column, cellValue, index){
      let status={1:"未审核",2:"审核中",3:"未通过",4:"已通过"}
      return status[cellValue];
    },
    async getApplicationList() {
      let that = this;
      
      let { data } = await axios({
        url: "/api/index/get_application_list",
        method: "post"
      });
      if (data.code == 0) that.pageInfo = data.data;
      that.pageLoading=false
    },

    async getTypeList() {
      let that = this;
      that.pageLoading=true
      let { data } = await axios({
        url: "/api/index/get_type_list",
        method: "post"
      });
      if (data.code == 0) {
        that.typeList = data.data;
      }
    },
    async getTagsList(){
      let that=this
      let {data}=await axios({url:"/api/index/get_tags_list"})
      if(data.code==0){
        that.tagList=data.data
      }
    }
  },
  async mounted() {
    let that = this;
    await that.getTypeList();
    await that.getApplicationList();
    await that.getTagsList()
  }
};
</script>

