<template>
  <el-button @click.stop="dialogShow=true">
    上传
    <el-dialog
      :visible.sync="dialogShow"
      title="新增软件"
      @close="dialogShow=false"
      :append-to-body="true"
      :modal-append-to-body="true"
      lock-scroll
      :close-on-click-modal="false"
      :close-on-press-escape="false"
    >
      <el-form ref="refForm" :rules="rule" :label-position="'right'" :model="form">
        <el-form-item label="软件名（英文）" prop="application_name">
          <el-input type="text" v-model="form.application_name" placeholder="包名" />
        </el-form-item>
        <el-form-item label="软件名（中文）" prop="application_name_zh">
          <el-input type="text" v-model="form.application_name_zh" placeholder="软件包的中文名称"  />
        </el-form-item>
        <el-form-item label="版本" prop="version">
          <el-input type="text" v-model="form.version" placeholder="请按照标准设置版本号" />
        </el-form-item>
        <el-form-item label="分类" prop="type_id">
          <el-select v-model="form.type_id" placeholder="所属分类">
            <el-option v-for="item in typeList" :key="item.id" :label="item.name" :value="item.id"></el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="作者" prop="author">
          <el-input type="text" v-model="form.author" placeholder="软件作者" />
        </el-form-item>
        <el-form-item label="维护者" prop="contributor">
          <el-input type="text" v-model="form.contributor" placeholder="软件维护者或者上传者" />
        </el-form-item>
        <el-form-item label="作者主页" prop="website">
          <el-input type="text" v-model="form.website" placeholder="作者的网站主页" />
        </el-form-item>
        <el-form-item label="说明" prop="more">
          <el-input type="text" v-model="form.more" placeholder="对应用用途等的一些说明文字" />
        </el-form-item>
        <el-form-item label="联系邮箱" prop="mail">
          <el-input type="text" v-model="form.mail" placeholder="维护者的邮箱" />
        </el-form-item>
        <el-form-item label="测试情况" prop="remark">
          <el-input type="text" v-model="form.remark" placeholder="写明在何种平台的测试情况" />
        </el-form-item>
        <el-form-item label="标签" prop="tags">
          <el-select clearable  multiple v-model="form.tags" placeholder="选择适合的标签" >
          <el-tooltip class="item" :key="item.value" v-for="item in tagList" effect="dark" :content="item.desc" placement="right-start">
            <el-option
            :key="item.value"
            :label="item.name"
            :value="item.value"
            >
            </el-option>
          </el-tooltip>
          </el-select>
        </el-form-item>
        <el-form-item label="图标" prop="icons">
          <div class="img-warp">
            <div class="img-box" v-if="form.icons" >
              <img :src="form.icons" />
            </div>
            <el-upload
            v-if="!form.icons"
              class="img-upload-box"
              ref="upload_icons"
              :limit="1"
              :action="iconsOssConfig.host"
              multiple
              accept="image/*"
              :disabled="iconsUploadLoading"
              :show-file-list="false"
              :before-upload="iconsBeforeUpload"
              :on-success="iconsUploadSuccess"
              :data="iconsDataOss"
            >
              <i class="el-icon-plus" v-loading="iconsUploadLoading"></i>
            </el-upload>
          </div>
        </el-form-item>
        <el-form-item label="软件截图（最多五张）" prop="img_urls">
          <div class="img-warp">
            <div class="img-box" v-for="(str,index) of form.img_urls" :key="index">
              <img :src="str" />
              <div class="img-btn">
                <div class="img-item-box">
                  <i class="el-icon-delete" @click.stop="delProtocol(index)"></i>
                </div>
                <div class="img-item-box">
                  <i class="el-icon-view" @click.stop="preview.index=index;preview.show=true"></i>
                </div>
              </div>
            </div>
            <el-upload
              class="img-upload-box"
              ref="upload_img"
              :limit="5"
              :action="imgOssConfig.host"
              multiple
              accept="image/*"
              :disabled="imgUploadLoading"
              :show-file-list="false"
              :before-upload="imgBeforeUpload"
              :on-success="imgUploadSuccess"
              :data="imgDataOss"
            >
              <i class="el-icon-plus" v-loading="imgUploadLoading"></i>
            </el-upload>
          </div>
        </el-form-item>
        <el-form-item label="软件包本体" prop="deb_url">
          <el-upload
            class="img-upload-box"
            ref="upload_deb"
            :action="debOssConfig.host"
            accept=".deb"
            :disabled="debUploadLoading"
            :on-progress="uploadFileProcess"
            :show-file-list="false"
            :before-upload="debBeforeUpload"
            :on-success="debUploadSuccess"
            :data="debDataOss"
          >
            <el-button size="small" type="primary">{{debUploadLoading?"正在上传": "点击上传"}}</el-button>
            <div slot="tip" class="el-upload__tip">
              只能上传deb文件
              <el-progress v-if="progressShow" :percentage="percentage"></el-progress>
            </div>
          </el-upload>
        </el-form-item>
      </el-form>
      <div slot="footer">
        <el-button @click="save" v-loading="saveLoading">保存</el-button>
      </div>
    </el-dialog>
  </el-button>
</template>

<script>
const deepCopy = require("deepcopy");
import uuidv1 from "uuid/v1";
import axios from "axios";
export default {
  props: {
    tagList:{
      type: Array,
      default: () => {
        return [];
      }
    },
    typeList: {
      type: Array,
      default: () => {
        return [];
      }
    }
  },
  data() {
    return {
      dialogShow: false,
      percentage: 0,
      tempFileList: {},
      progressShow: false,
      uploadStatus: "",

      imgUploadLoading: false,
      debUploadLoading: false,
      iconsUploadLoading: false,

      imgDataOss: {},
      debDataOss: {},
      iconsDataOss: {},

      saveLoading: false,

      preview: {
        show: false,
        index: ""
      },

      imgOssConfig: { host: "/" },
      debOssConfig: { host: "/" },
      iconsOssConfig: { host: "/" },

      form: {
        application_name: "",
        contributor:"",
        icons:"",
        application_name_zh:"",
        size:0,
        file_name:"",
        website:"",
        version:"",
        more:"",
        type_id: null,
        author: "",
        remark: "",
        img_urls: [],
        deb_url: "",
        mail: "",
        tags:[]
      },
      rule:{application_name: [{required: true, message: '请输入程序名称（安装用）', trigger: 'blur'}],
        contributor:[{required: true, message: '请输入维护者', trigger: 'blur'}],
        icons:[{required: true, message: '请上传图标', trigger: 'blur'}],
        application_name_zh:[{required: true, message: '请输入程序中文名称（显示用）', trigger: 'blur'}],
        website:[{required: true, message: '请输入作者主页', trigger: 'blur'}],
        version:[{required: true, message: '请输入版本', trigger: 'blur'}],
        more:[{required: true, message: '请输入说明', trigger: 'blur'}],
        type_id:[{required: true, message: '请选择分类', trigger: 'blur'}],
        author: [{required: true, message: '请输入作者', trigger: 'blur'}],
        remark: [{required: true, message: '请输入测试情况', trigger: 'blur'}],
        img_urls: [{type: 'array',required: true, message: '请上传至少一张图片', trigger: 'blur'}],
        deb_url: [{required: true, message: '请上传软件包', trigger: 'blur'}],
        mail: [{type: 'email',required: true, message: '请输入正确邮箱', trigger: 'blur'}],
        tags:[{type: 'array',required: true, message: '请选择至少一个标签', trigger: 'blur'}],
        }
    };
  },
  watch: {
    ["dialogShow"](curr, old) {
      let that = this;
      if (!curr) {
        setTimeout(function() {
          that.progressShow = false;
          that.percentage = 0;
          that.form = deepCopy(that.copyForm);
          that.$refs["refForm"].clearValidate();
        }, 500);
      }
    }
  },
  methods: {
    async save() {
      let that = this;
      that.$refs['refForm'].validate(async (valid) => {
        if(!valid) return false
        that.saveLoading = true;
        let { data } = await axios({
          url: "/api/index/upload_application",
          method: "post",
          data: that.form
        });
        if (data.code == 0) {
          that.saveLoading = false;
          that.dialogShow = false;
          that.$emit('saveSuccess');
        } else {
          that.saveLoading = false;
          that.$message(data.msg, "error");
        }
      })
      
    },
    uploadFileProcess(event, file, fileList) {
      let that = this;
      that.percentage = Math.round(file.percentage * 100) / 100;
    },
    // 图片上传陈成功
    imgUploadSuccess(err, file) {
      let that = this;
      that.form.img_urls.push(that.tempFileList[file.name]);
    },
    iconsUploadSuccess(err, file) {
      let that = this;
      that.form.icons=that.tempFileList[file.name];
    },
    // deb上传成功
    debUploadSuccess(err, file) {
      let that = this;
      console.log(file);
      that.debUploadLoading=false
      that.percentage = 100;
      that.form.size=file.size;
      that.form.file_name=file.name
      that.form.deb_url = that.tempFileList[file.name];
    },
    //删除图片
    delProtocol(index) {
      let that = this;
      that.form.img_urls.splice(index, 1);
    },
    // 获取图片上传的数据
    async imgBeforeUpload(file) {
      let that = this;
      let { data } = await axios({
        url: "/api/index/upload_pic"
      });
      if (data.code != 0) {
        that.$message("获取上传签名错误", "error");
        return false;
      }
      that.imgOssConfig.host = data.data.host;
      let uuid = uuidv1();
      let path = data.data.dir;
      let file_name = path + uuid + '.' +file.name.split('.')[file.name.split('.').length-1].replace(/\+/g,'_plus_');
      that.imgDataOss = {
        key: file_name, //上传的路径
        OSSAccessKeyId: data.data.OSSAccessKeyId, //osskeyid
        policy: data.data.policy, //授权
        signature: data.data.signature, //签名
        success_action_status: "200" //返回值
      };
      that.tempFileList[file.name] = data.data.host + file_name;
    },
    // 获取图片上传的数据
    async iconsBeforeUpload(file) {
      let that = this;
      let { data } = await axios({
        url: "/api/index/upload_icons"
      });
      if (data.code != 0) {
        that.$message("获取上传签名错误", "error");
        return false;
      }
      that.iconsOssConfig.host = data.data.host;
      let uuid = uuidv1();
      let path = data.data.dir;
      let file_name = path + uuid +'.' +file.name.split('.')[file.name.split('.').length-1].replace(/\+/g,'_plus_');
      that.iconsDataOss = {
        key: file_name, //上传的路径
        OSSAccessKeyId: data.data.OSSAccessKeyId, //osskeyid
        policy: data.data.policy, //授权
        signature: data.data.signature, //签名
        success_action_status: "200" //返回值
      };
      that.tempFileList[file.name] = data.data.host + file_name;
    },

    async debBeforeUpload(file) {
      let that = this;
      if(file.type!="application/vnd.debian.binary-package"){
        that.$message("文件类型错误", "error");
        return false;
      }
      let { data } = await axios({
        url: "/api/index/upload_deb"
      });
      if (data.code != 0) {
        that.$message("获取上传签名错误", "error");
        return false;
      }
      that.debUploadLoading=true
      that.progressShow = true;
      that.debOssConfig.host = data.data.host;
      let uuid = uuidv1();
      let path = data.data.dir;

      let file_name = path + uuid + file.name.replace(/\+/g,'_plus_');
      that.debDataOss = {
        key: file_name, //上传的路径
        OSSAccessKeyId: data.data.OSSAccessKeyId, //osskeyid
        policy: data.data.policy, //授权
        signature: data.data.signature, //签名
        success_action_status: "200" //返回值
      };
      that.tempFileList[file.name] = data.data.host + file_name;
    }
  },
  mounted() {
    let that = this;
    that.copyForm = deepCopy(that.form);
  }
};
</script>